import routeEnter from '../routeEnter.js'

module.exports = Object.assign({
    path: "delegateFund",
    breadcrumbName: "委托资金",
    getIndexRoute(nextState, callback) {
        require.ensure([], function (require) {
            callback(null, {
                breadcrumbName: '列表',
                component: require('./components/list.jsx')
            })
        }, 'delegateFund_list')
    },
    getChildRoutes(partialNextState, cb) {
        require.ensure([], (require) => {
            cb(null, [
                {
                    path: 'detail/:id',
                    breadcrumbName:'详情',
                    getComponent(nextState, cb) {
                        require.ensure([], (require) => {
                            cb(null, require('./components/detail.jsx'))
                        },'delegateFund_detail')
                    }
                },
                {
                    path: 'edit/:id',
                    breadcrumbName:'修改',
                    getComponent(nextState, cb) {
                        require.ensure([], (require) => {
                            cb(null, require('./components/edit.jsx'))
                        },'delegateFund_edit')
                    }
                },
                {
                    path: 'new',
                    breadcrumbName:'新增',
                    getComponent(nextState, cb) {
                        require.ensure([], (require) => {
                            cb(null, require('./components/new.jsx'))
                        },'delegateFund_new')
                    }
                },
                {
                    path: 'receivedProject/list',
                    breadcrumbName:'收到的投递项目列表',
                    getComponent(nextState, cb) {
                        require.ensure([], (require) => {
                            cb(null, require('./components/receivedProject/list.jsx'))
                        },'delegateFund_receivedProject_list')
                    }
                },
                {
                    path: 'receivedProject/detail/:id',
                    breadcrumbName:'收到的投递项目详情',
                    getComponent(nextState, cb) {
                        require.ensure([], (require) => {
                            cb(null, require('./components/receivedProject/detail.jsx'))
                        },'delegateFund_receivedProject_detail')
                    }
                }
            ])
        })
    }
}, routeEnter);



