import routeEnter from 'routeEnter'

module.exports = Object.assign({
    path: "help",
    breadcrumbName: "帮助中心",
    getChildRoutes(partialNextState, cb) {
        require.ensure([], (require) => {
            cb(null, [
                require("./components/qa"),
                require("./components/feedback")
            ])
        }, 'help')
    }
}, routeEnter);



