import routeEnter from '../routeEnter.js'

module.exports = Object.assign({
    path: "activity",
    breadcrumbName: "活动管理",
    getIndexRoute(nextState, callback) {
        require.ensure([], function (require) {
            callback(null, {
                breadcrumbName: '列表',
                component: require('./components/list.jsx')
            })
        }, 'activity_list')
    },
    getChildRoutes(partialNextState, cb) {
        require.ensure([], (require) => {
            cb(null, [
                {
                    path: 'add',
                    breadcrumbName:'新增',
                    getComponent(nextState, cb) {
                        require.ensure([], (require) => {
                            cb(null, require('./components/add.jsx'))
                        },'activity_add')
                    }
                },
                {
                    path: 'edit/:id',
                    breadcrumbName:'修改',
                    getComponent(nextState, cb) {
                        require.ensure([], (require) => {
                            cb(null, require('./components/edit.jsx'))
                        },'activity_edit')
                    }
                },
                {
                    path: 'detail/:id',
                    breadcrumbName:'详情',
                    getComponent(nextState, cb) {
                        require.ensure([], (require) => {
                            cb(null, require('./components/detail.jsx'))
                        },'activity_detail')
                    }
                },
                {
                    path: 'signUp/:id',
                    breadcrumbName:'报名列表',
                    getComponent(nextState, cb) {
                        require.ensure([], (require) => {
                            cb(null, require('./components/signUp.jsx'))
                        },'activity_signUp')
                    }
                }
            ])
        })
    }
}, routeEnter);



