import routeEnter from '../../routeEnter.js'

module.exports = Object.assign({
    path: "user/mail",
    breadcrumbName: "群发邮件",
    getIndexRoute(nextState, callback) {
        require.ensure([], function (require) {
            callback(null, {
                breadcrumbName: '编辑',
                component: require('./components/edit.jsx')
            })
        }, 'user_mail_edit')
    },
    getChildRoutes(partialNextState, cb) {
        require.ensure([], (require) => {
            cb(null, [
                {
                    path: 'history',
                    breadcrumbName:'历史',
                    getComponent(nextState, cb) {
                        require.ensure([], (require) => {
                            cb(null, require('./components/history.jsx'))
                        },'user_mail_history')
                    }
                }
            ])
        })
    }
}, routeEnter);



