import routeEnter from '../routeEnter.js'

module.exports = Object.assign({
    path: "report",
    breadcrumbName: "举报管理",
    getIndexRoute(nextState, callback) {
        require.ensure([], function (require) {
            callback(null, {
                breadcrumbName: '列表',
                component: require('./components/list.jsx')
            })
        }, 'report_list')
    },
    getChildRoutes(partialNextState, cb) {
        require.ensure([], (require) => {
            cb(null, [
                {
                    path: 'detail/:id',
                    breadcrumbName: "详情",
                    getComponent(nextState, cb) {
                        require.ensure([], (require) => {
                            cb(null, require('./components/detail.jsx'))
                        },'report_detail')
                    }
                }
            ])
        })
    }
}, routeEnter);



