import routeEnter from 'routeEnter'

module.exports = Object.assign({
    path: "platform",
    breadcrumbName: "平台管理",
    getChildRoutes(partialNextState, cb) {
        require.ensure([], (require) => {
            cb(null, [
                require("./components/company"),
                require("./components/news"),
                require("./components/report"),
                require("./components/contact")
            ])
        }, 'platform')
    }
}, routeEnter);



