import routeEnter from '../../routeEnter.js'

module.exports = Object.assign({
    path: "auth/admin",
    breadcrumbName: "管理员及权限",
    getIndexRoute(nextState, callback) {
        require.ensure([], function (require) {
            callback(null, {
                breadcrumbName: '用户',
                component: require('./components/user.jsx')
            })
        }, 'auth_admin_user')
    },
    getChildRoutes(partialNextState, cb) {
        require.ensure([], (require) => {
            cb(null, [
                {
                    path: 'user',
                    breadcrumbName:'用户',
                    getComponent(nextState, cb) {
                        require.ensure([], (require) => {
                            cb(null, require('./components/user.jsx'))
                        },'auth_admin_user')
                    }
                }, {
                    path: 'project',
                    breadcrumbName:'项目',
                    getComponent(nextState, cb) {
                        require.ensure([], (require) => {
                            cb(null, require('./components/project.jsx'))
                        },'auth_admin_project')
                    }
                }, {
                    path: 'fund',
                    breadcrumbName:'资金',
                    getComponent(nextState, cb) {
                        require.ensure([], (require) => {
                            cb(null, require('./components/fund.jsx'))
                        },'auth_admin_fund')
                    }
                }, {
                    path: 'other',
                    breadcrumbName:'其他',
                    getComponent(nextState, cb) {
                        require.ensure([], (require) => {
                            cb(null, require('./components/other.jsx'))
                        },'auth_admin_other')
                    }
                }
            ])
        })
    }
}, routeEnter);



