import routeEnter from '../routeEnter.js'

module.exports = Object.assign({
    path: "delegateProject",
    breadcrumbName: "委托项目",
    getIndexRoute(nextState, callback) {
        require.ensure([], function (require) {
            callback(null, {
                breadcrumbName: '列表',
                component: require('./components/list.jsx')
            })
        }, 'delegateProject_list')
    },
    getChildRoutes(partialNextState, cb) {
        require.ensure([], (require) => {
            cb(null, [
                {
                    path: 'detail/:id',
                    breadcrumbName:'详情',
                    getComponent(nextState, cb) {
                        require.ensure([], (require) => {
                            cb(null, require('./components/detail.jsx'))
                        },'delegateProject_detail')
                    }
                },
                {
                    path: 'edit/:id',
                    breadcrumbName:'修改',
                    getComponent(nextState, cb) {
                        require.ensure([], (require) => {
                            cb(null, require('./components/edit.jsx'))
                        },'delegateProject_edit')
                    }
                },
                {
                    path: 'new',
                    breadcrumbName:'新增',
                    getComponent(nextState, cb) {
                        require.ensure([], (require) => {
                            cb(null, require('./components/new.jsx'))
                        },'delegateProject_new')
                    }
                },
                {
                    path: 'receivedFund/list',
                    breadcrumbName:'收到的投递资金列表',
                    getComponent(nextState, cb) {
                        require.ensure([], (require) => {
                            cb(null, require('./components/receivedFund/list.jsx'))
                        },'delegateProject_receivedFund_list')
                    }
                },
                {
                    path: 'receivedFund/detail/:id',
                    breadcrumbName:'收到的投递资金详情',
                    getComponent(nextState, cb) {
                        require.ensure([], (require) => {
                            cb(null, require('./components/receivedFund/detail.jsx'))
                        },'delegateProject_receivedFund_detail')
                    }
                }
            ])
        })
    }
}, routeEnter);



